@import url(https://fonts.googleapis.com/css?family=Indie+Flower&display=swap);
@import url(https://cdn.rawgit.com/konpa/devicon/df6431e323547add1b4cf45992913f15286456d3/devicon.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.background{
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(245, 246, 252, 0.459)), to(rgba(26, 27, 26, 0.945))),url(/my-blog/static/media/cover-removebg.783c2b97.png);
    background-image: linear-gradient(to bottom, rgba(245, 246, 252, 0.459), rgba(26, 27, 26, 0.945)),url(/my-blog/static/media/cover-removebg.783c2b97.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}


